export default {
	props:{
		item:Object,
		index:Number
	},
	
	data() {
		return {
			
		};
	},
	methods:{
		clickevent(){
			console.log("进入")
			switch (this.item.clicktype){
				case "navigateTo":
					if(this.item.url){uni.navigateTo({url: this.item.url})}
					break;
				case "clear":
					uni.showModal({
						title: '提示',
						content: '是否清除缓存',
						showCancel: false,
						cancelText: '',
						confirmText: '立即清除',
						success: res => {
							if(res.confirm){
								uni.clearStorage()
								this.User.logout(false)
								uni.showToast({
									title: '清除缓存成功!'
								});
							}
						}
					});
					break;
			}
		}
	}
}
